@font-face {
    font-family: "Futura";
    src: url('../assets/fonts/FuturaLTProBook.otf');
}


.container{
    max-width: 1320px;
    margin: 0 auto;
}

.form{
    text-align: center;
    margin-top: 40px;
    label{
        font-size: 18px;
        font-family: "Futura";
    }
    input[type=email]{
        width: 300px;
        margin-top: 5px;
        margin-bottom: 20px;
        background-color: transparent;
        border: none;
        border: 1px solid black;
        border-radius: 2px;
        padding: 5px 10px;
        &:focus{
            outline: none;
        }
    }
    input[type=password]{
        width: 300px;
        margin-top: 5px;
        margin-bottom: 20px;
        background-color: transparent;
        border: none;
        border: 1px solid black;
        border-radius: 2px;
        padding: 5px 10px;
        &:focus{
            outline: none;
        }
    }
    button{
        margin-top: 40px;
        color: black;
        font-family: "Futura";
        cursor: pointer;
        border: none;
        border-bottom: 2px solid black;
        padding: 2px 0;
        background-color: transparent;
        font-size: 18px;
    }
    div{
        font-size: 18px;
        font-family: "Futura";
        a{
            text-decoration: none;
            color: black;
            font-family: "Futura";
            cursor: pointer;
            border: none;
            border-bottom: 2px solid black;
            background-color: transparent;
            font-size: 18px;
        }
    }
}