@font-face {
    font-family: "Trajan";
    src: url('../../assets/fonts/Trajan.ttf');
}
@font-face {
    font-family: "Futura";
    src: url('../../assets/fonts/FuturaLTProBook.otf');
}


*{
    margin: 0;
    padding: 0;
}
.container{
    max-width: 1320px;
    margin: 0 auto;
}
.header{
    font-family: "Futura";
    max-height: 70px;
    &__content{
        display: flex;
        height: 70px;
        align-items: center;
    }
    &__item{
        list-style-type: none;
        margin-right: 15px;
        cursor: pointer;
        user-select: none;
        border-bottom: 2px solid transparent;
        &:hover{
            border-bottom: 2px solid black;
        }
        &:last-of-type{
            margin-right: 0;
        }
        & a{
            color: black;
            text-decoration: none;
        }
    }
    &__logo{
        font-family: 'Trajan';
        font-size: 22px;
        flex: 1.11;
        margin-top: 5px;
        cursor: pointer;
        user-select: none;
        & a{
            color: black;
            text-decoration: none;
        }
    }
    &__links{
        display: flex;
        align-items: center;
    }
    &__button{
        list-style-type: none;
        margin-right: 15px;
        cursor: pointer;
        user-select: none;
        border-bottom: 2px solid transparent;
        &:hover{
            border-bottom: 2px solid black;
        }
        &:last-of-type{
            margin-right: 0;
        }
        &:last-of-type:hover{
            border-color: transparent;
        }
        & a{
            color: black;
            text-decoration: none;
        }
    }
    &__icon{
        width: 25px;
        height: 25px;
        margin-top: 10px;
    }
}