@font-face {
    font-family: "Futura";
    src: url('../../assets/fonts/FuturaLTProBook.otf');
}

.table{
    margin-top: 20px;
    display: flex;
    justify-content: center;
    table{
        text-align: center;
        border: 2px solid black;
        border-collapse: collapse;
    }
    td{
        font-family: "Futura";
        padding: 20px 90px;
        border: 2px solid black;
        border-collapse: collapse;
        a{
            text-decoration: none;
            color: black;
            font-family: "Futura";
            cursor: pointer;
            border-bottom: 2px solid black;
            padding: 2px;
        }
    }
}