@font-face {
    font-family: "Futura";
    src: url('../assets/fonts/FuturaLTProBook.otf');
}


.container{
    max-width: 1320px;
    margin: 0 auto;
}

.form{
    margin-top: 40px;
    label{
        font-size: 18px;
        font-family: "Futura";
    }
    input[type=text]{
        margin-left: 30px;
        margin-bottom: 20px;
        background-color: transparent;
        border: none;
        border: 1px solid black;
        border-radius: 2px;
        padding: 5px 10px;
        &:focus{
            outline: none;
        }
    }
    input[type=number]{
        margin-left: 24px;
        margin-bottom: 20px;
        background-color: transparent;
        border: none;
        border: 1px solid black;
        border-radius: 2px;
        padding: 5px 10px;
        &:focus{
            outline: none;
        }
    }
    input[type=file]{
        margin-left: 13px;
        margin-bottom: 20px;
    }
    button{
        color: black;
        font-family: "Futura";
        cursor: pointer;
        border: none;
        border-bottom: 2px solid black;
        padding: 2px;
        background-color: transparent;
        font-size: 18px;
    }
}