@font-face {
    font-family: "Trajan";
    src: url('../../assets/fonts/Trajan.ttf');
}
@font-face {
    font-family: "Futura";
    src: url('../../assets/fonts/FuturaLTProBook.otf');
}

.container{
    max-width: 1320px;
    margin: 0 auto;
}

.products{
    margin-top: 60px;
    margin-bottom: 60px;
    &__title{
        font-family: "Trajan";
        & a{
            text-decoration: none;
            color: black;
        }
    }
    &__content{
        margin-right: -40px;
        display: flex;
        flex-wrap: wrap;
    }
}