@font-face {
    font-family: "Futura";
    src: url('../../../assets/fonts/FuturaLTProBook.otf');
}

.product{
    &__item{
        margin-top: 40px;
        margin-right: 40px;
        font-family: "Futura";
        font-size: 20px;
        width: 300px;
        display: flex;
        flex-direction: column;
    }
    &__image{
        width: 300px;
    }
    &__title{
        margin-top: 15px;
        flex: 2;
    }
    &__price{
        user-select: none;
        &::after{
            content: "$";
        }
    }
    &__button{
        cursor: pointer;
        border-bottom: 2px solid black;
        padding: 2px;
    }
    &__bottom{
        display: flex;
        margin-top: 30px;
        flex: 1;
        justify-content: space-between;
        align-items: center;
    }
}